	module.exports = {"data":{"postsJson":{"title":"Jesus is the Christ","questionsTitle":"HUNGERING TO KNOW GOD MORE","display":"block","bookName":"Mark","ref":"8:1-9:50","html":"<p>   <strong>8</strong><u>1</u>In those days, when there was a very great multitude, and they had nothing to eat, Jesus called his disciples to himself, and said to them,</p><h1>JESUS</h1><h2><u>2</u>I have compassion on the multitude, because they have stayed with me now three days, and have nothing to eat. <u>3</u>If I send them away fasting to their home, they will faint on the way, for some of them have come a long way.</h2><p>   <u>4</u>His disciples answered him,</p><h1>THE DISCIPLES</h1><h3>From where could one satisfy these people with bread here in a deserted place?</h3><p>   <u>5</u>He asked them,</p><h1>JESUS</h1><h2>How many loaves do you have?</h2><p>   They said,</p><h1>THE DISCIPLES</h1><h3>Seven.</h3><p>   <u>6</u>He commanded the multitude to sit down on the ground, and he took the seven loaves. Having given thanks, he broke them, and gave them to his disciples to serve, and they served the multitude. <u>7</u>They had a few small fish. Having blessed them, he said to serve these also. <u>8</u>They ate, and were filled. They took up seven baskets of broken pieces that were left over. <u>9</u>Those who had eaten were about four thousand. Then he sent them away. <br />   <u>10</u>Immediately he entered into the boat with his disciples, and came into the region of Dalmanutha. <u>11</u>The Pharisees came out and began to question him, seeking from him a sign from heaven, and testing him. <u>12</u>He sighed deeply in his spirit, and said,</p><h1>JESUS</h1><h2>Why does this generation* seek a sign? Most certainly I tell you, no sign will be given to this generation.</h2><p>   <u>13</u>He left them, and again entering into the boat, departed to the other side. <u>14</u>They forgot to take bread; and they didn’t have more than one loaf in the boat with them. <u>15</u>He warned them, saying,</p><h1>JESUS</h1><h2>Take heed: beware of the yeast of the Pharisees and the yeast of Herod.</h2><p>   <u>16</u>They reasoned with one another, saying,</p><h1>THE DISCIPLES</h1><h3>It’s because we have no bread.</h3><p>   <u>17</u>Jesus, perceiving it, said to them,</p><h1>JESUS</h1><h2>Why do you reason that it’s because you have no bread? Don’t you perceive yet, neither understand? Is your heart still hardened? <u>18</u>Having eyes, don’t you see? Having ears, don’t you hear? Don’t you remember? <u>19</u>When I broke the five loaves among the five thousand, how many baskets full of broken pieces did you take up?</h2><p>   They told him,</p><h1>THE TWELVE DISCIPLES</h1><h3>Twelve.</h3><h1>JESUS</h1><h2><u>20</u>When the seven loaves fed the four thousand, how many baskets full of broken pieces did you take up?</h2><p>   They told him,</p><h1>THE DISCIPLES</h1><h3>Seven.</h3><p>   <u>21</u>He asked them,</p><h1>JESUS</h1><h2>Don’t you understand, yet?</h2><p>   <u>22</u>He came to Bethsaida. They brought a blind man to him, and begged him to touch him. <u>23</u>He took hold of the blind man by the hand, and brought him out of the village. When he had spit on his eyes, and laid his hands on him, he asked him if he saw anything. <br />   <u>24</u>He looked up, and said,</p><h1>A BLIND MAN FROM BETHSAIDA</h1><h4>I see men; for I see them like trees walking.</h4><p>   <u>25</u>Then again he laid his hands on his eyes. He looked intently, and was restored, and saw everyone clearly. <u>26</u>He sent him away to his house, saying,</p><h1>JESUS</h1><h2>Don’t enter into the village, nor tell anyone in the village.</h2><p>   <u>27</u>Jesus went out, with his disciples, into the villages of Caesarea Philippi. On the way he asked his disciples,</p><h1>JESUS</h1><h2>Who do men say that I am?</h2><p>   <u>28</u>They told him,</p><h1>THE DISCIPLES</h1><h3>John the Baptizer, and others say Elijah, but others: one of the prophets.</h3><p>   <u>29</u>He said to them,</p><h1>JESUS</h1><h2>But who do you say that I am?</h2><p>   Peter answered,</p><h1>SIMON PETER</h1><h3>You are the Christ.</h3><p>   <u>30</u>He commanded them that they should tell no one about him. <u>31</u>He began to teach them that the Son of Man must suffer many things, and be rejected by the elders, the chief priests, and the scribes, and be killed, and after three days rise again. <u>32</u>He spoke to them openly. Peter took him, and began to rebuke him. <u>33</u>But he, turning around, and seeing his disciples, rebuked Peter, and said,</p><h1>JESUS</h1><h2>Get behind me, Satan! For you have in mind not the things of God, but the things of men.</h2><p>   <u>34</u>He called the multitude to himself with his disciples, and said to them,</p><h1>JESUS</h1><h2>Whoever wants to come after me, let him deny himself, and take up his cross, and follow me. <u>35</u>For whoever wants to save his life will lose it; and whoever will lose his life for my sake and the sake of the Good News will save it. <u>36</u>For what does it profit a man, to gain the whole world, and forfeit his life? <u>37</u>For what will a man give in exchange for his life? <u>38</u>For whoever will be ashamed of me and of my words in this adulterous and sinful generation, the Son of Man also will be ashamed of him, when he comes in his Father’s glory, with the holy angels.</h2><p>   <strong>9</strong><u>1</u>He said to them,</p><h1>JESUS</h1><h2>Most certainly I tell you, there are some standing here who will in no way taste death until they see God’s Kingdom come with power.</h2><p>   <u>2</u>After six days Jesus took with him Peter, James, and John, and brought them up onto a high mountain privately by themselves, and he was changed into another form in front of them. <u>3</u>His clothing became glistening, exceedingly white, like snow, such as no launderer on earth can whiten them. <u>4</u>Elijah and Moses appeared to them, and they were talking with Jesus. <br />   <u>5</u>Peter answered Jesus,</p><h1>SIMON PETER</h1><h3>Rabbi, it is good for us to be here. Let’s make three tents: one for you, one for Moses, and one for Elijah.</h3><p><u>6</u>For he didn’t know what to say, for they were very afraid. <br />   <u>7</u>A cloud came, overshadowing them, and a voice came out of the cloud,</p><h1>GOD</h1><h2>This is my beloved Son. Listen to him.</h2><p>   <u>8</u>Suddenly looking around, they saw no one with them any more, except Jesus only. <br />   <u>9</u>As they were coming down from the mountain, he commanded them that they should tell no one what things they had seen, until after the Son of Man had risen from the dead. <u>10</u>They kept this saying to themselves, questioning what the “rising from the dead” meant. <br />   <u>11</u>They asked him, saying,</p><h1>THREE DISCIPLES</h1><h3>Why do the scribes say that Elijah must come first?</h3><p>   <u>12</u>He said to them,</p><h1>JESUS</h1><h2>Elijah indeed comes first, and restores all things. How is it written about the Son of Man, that he should suffer many things and be despised? <u>13</u>But I tell you that Elijah has come, and they have also done to him whatever they wanted to, even as it is written about him.</h2><p>   <u>14</u>Coming to the disciples, he saw a great multitude around them, and scribes questioning them. <u>15</u>Immediately all the multitude, when they saw him, were greatly amazed, and running to him greeted him. <u>16</u>He asked the scribes,</p><h1>JESUS</h1><h2>What are you asking them?</h2><p>   <u>17</u>One of the multitude answered,</p><h1>AN EPILEPTIC’S FATHER</h1><h4>Teacher, I brought to you my son, who has a mute spirit; <u>18</u>and wherever it seizes him, it throws him down, and he foams at the mouth, and grinds his teeth, and wastes away. I asked your disciples to cast it out, and they weren’t able.</h4><p>   <u>19</u>He answered him,</p><h1>JESUS</h1><h2>Unbelieving generation, how long shall I be with you? How long shall I bear with you? Bring him to me.</h2><p>   <u>20</u>They brought him to him, and when he saw him, immediately the spirit convulsed him, and he fell on the ground, wallowing and foaming at the mouth. <br />   <u>21</u>He asked his father,</p><h1>JESUS</h1><h2>How long has it been since this has come to him?</h2><p>   He said,</p><h1>AN EPILEPTIC’S FATHER</h1><h4>From childhood. <u>22</u>Often it has cast him both into the fire and into the water, to destroy him. But if you can do anything, have compassion on us, and help us.</h4><p>   <u>23</u>Jesus said to him,</p><h1>JESUS</h1><h2>If you can believe, all things are possible to him who believes.</h2><p>   <u>24</u>Immediately the father of the child cried out with tears,</p><h1>AN EPILEPTIC’S FATHER</h1><h4>I believe. Help my unbelief!</h4><p>   <u>25</u>When Jesus saw that a multitude came running together, he rebuked the unclean spirit, saying to him,</p><h1>JESUS</h1><h2>You mute and deaf spirit, I command you, come out of him, and never enter him again!</h2><p>   <u>26</u>Having cried out, and convulsed greatly, it came out of him. The boy became like one dead; so much that most of them said,</p><h1>THE CROWD</h1><h4>He is dead.</h4><p><u>27</u>But Jesus took him by the hand, and raised him up; and he arose. <br />   <u>28</u>When he had come into the house, his disciples asked him privately,</p><h1>THE DISCIPLES</h1><h3>Why couldn’t we cast it out?</h3><p><u>29</u>He said to them,</p><h1>JESUS</h1><h2>This kind can come out by nothing, except by prayer and fasting.</h2><p>   <u>30</u>They went out from there, and passed through Galilee. He didn’t want anyone to know it. <u>31</u>For he was teaching his disciples, and said to them,</p><h1>JESUS</h1><h2>The Son of Man is being handed over to the hands of men, and they will kill him; and when he is killed, on the third day he will rise again.</h2><p>   <u>32</u>But they didn’t understand the saying, and were afraid to ask him. <br />   <u>33</u>He came to Capernaum, and when he was in the house he asked them,</p><h1>JESUS</h1><h2>What were you arguing among yourselves on the way?</h2><p>   <u>34</u>But they were silent, for they had disputed one with another on the way about who was the greatest. <br />   <u>35</u>He sat down, and called the twelve; and he said to them,</p><h1>JESUS</h1><h2>If any man wants to be first, he shall be last of all, and servant of all.</h2><p><u>36</u>He took a little child, and set him in the middle of them. Taking him in his arms, he said to them,</p><h1>JESUS</h1><h2><u>37</u>Whoever receives one such little child in my name, receives me, and whoever receives me, doesn’t receive me, but him who sent me.</h2><p>   <u>38</u>John said to him,</p><h1>JOHN THE DISCIPLE</h1><h3>Teacher, we saw someone who doesn’t follow us casting out demons in your name; and we forbade him, because he doesn’t follow us.</h3><p>   <u>39</u>But Jesus said,</p><h1>JESUS</h1><h2>Don’t forbid him, for there is no one who will do a mighty work in my name, and be able quickly to speak evil of me. <u>40</u>For whoever is not against us is on our side. <u>41</u>For whoever will give you a cup of water to drink in my name, because you are Christ’s, most certainly I tell you, he will in no way lose his reward. <u>42</u>Whoever will cause one of these little ones who believe in me to stumble, it would be better for him if he were thrown into the sea with a millstone hung around his neck. <u>43</u>If your hand causes you to stumble, cut it off. It is better for you to enter into life maimed, rather than having your two hands to go into Gehenna,* into the unquenchable fire, <u>44</u>‘where their worm doesn’t die, and the fire is not quenched.’* <u>45</u>If your foot causes you to stumble, cut it off. It is better for you to enter into life lame, rather than having your two feet to be cast into Gehenna,* into the fire that will never be quenched— <u>46</u>‘where their worm doesn’t die, and the fire is not quenched.’ <u>47</u>If your eye causes you to stumble, cast it out. It is better for you to enter into God’s Kingdom with one eye, rather than having two eyes to be cast into the Gehenna* of fire, <u>48</u>‘where their worm doesn’t die, and the fire is not quenched.’* <u>49</u>For everyone will be salted with fire, and every sacrifice will be seasoned with salt. <u>50</u>Salt is good, but if the salt has lost its saltiness, with what will you season it? Have salt in yourselves, and be at peace with one another.</h2><h5>*Mark 8:12 The word translated “generation” here (genea) could also be translated “people”, “race”, or “family”. <br />*Mark 9:43 or, Hell <br />*Mark 9:44 Isaiah 66:24 <br />*Mark 9:45 or, Hell <br />*Mark 9:47 or, Hell <br />*Mark 9:48 Isaiah 66:24 </h5>","path":"/320","Question1":"Meditate on the acts of God described in this portion of Scripture.","Question2":"What do these clarify about the heart of God?","Question3":"Have you experienced this expression of the heart of God in your life? If yes, how so? If not, what steps can you take to experience this?","Question4":"What can you do to grow in your desire to know and obey God more intimately in your life?","prevPath":"/319","nextPath":"/321"}},"pathContext":{}}

